import { createCrudStore } from '../../../lib/store-factory';
// REMOVED: import { data as initialData, penaltyTypesData } from './data'; // Mock data no longer used - database is source of truth
import type { Penalty, PenaltyType } from './types';

export const usePenaltyStore = createCrudStore<Penalty>([], 'penalties', {
  businessIdField: 'id',
  apiEndpoint: '/api/settings/data?type=penalty',
});

export const usePenaltyTypeStore = createCrudStore<PenaltyType>([], 'penalties', {
  businessIdField: 'id',
  apiEndpoint: '/api/settings/data?type=penalty-type',
});
